@import './variables';

* {
    padding: 0%;
    margin: 0%;
    border: 0;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    line-height: 1;
    font-size: 100%;
    text-size-adjust: 100%;
}

html {
    font-size: 62.5% !important;

    @media screen and (max-width: $bp-large) {
        font-size: 50% !important;
    }
}

main {
    min-width: 375px;
}

input,
button,
textarea {
    font-family: inherit;
}

input :-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0%;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
a:active {
    outline: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}
